<script>
export default {
  name: "Map",
  methods: {
    changeCountry(selectedCountry) {
      this.$store.commit("changeCountry", selectedCountry);
    },
  },
  mounted() {
    window.TurnMapOn();
    let supportedCountries = ["united-kingdom", "france", "russian-federation"];
    setTimeout(() => {
      supportedCountries.forEach((link) => {
        document
          .querySelector("." + link)
          .parentElement.classList.add("active-region");
      });
    }, 10);
  },
};
</script>

